// https://github.com/uxitten/polyfill/blob/master/string.polyfill.js
// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/repeat
if (!String.prototype.padStart) {
  String.prototype.padStart = function padStart(targetLength, padString) {
    targetLength = targetLength >> 0; //floor if number or convert non-number to 0;
    padString = String(padString || ' ');
    if (this.length > targetLength) {
      return String(this);
    } else {
      targetLength = targetLength - this.length;
      if (targetLength > padString.length) {
        padString += padString.repeat(targetLength / padString.length); //append to original to ensure we are longer than needed
      }
      return padString.slice(0, targetLength) + String(this);
    }
  };
}

Date.prototype.format = function () {
  var year = this.getFullYear()
  var month = (this.getMonth() + 1).toString().padStart(2, '0')
  var day = this.getDate().toString().padStart(2, '0')
  return `${year}${month}${day}`
}

/**
 * get the first date for the specified date according to local time. 
 * @param {date} date 
 */
function firstDayOfWeek(date = new Date()) {
  var dayOfWeek = date.getDay()
  var day = date.getDate()
  var firstDay = day - dayOfWeek
  date.setDate(firstDay)
  return date
}

/**
 * get the last date for the specified date according to local time. 
 * @param {*} date 
 */
function lastDayOfWeek(date = new Date()) {
  var dayOfWeek = date.getDay()
  var day = date.getDate()
  // week (0-6) 
  var timespan = 6 - dayOfWeek
  var lastDay = day + timespan
  date.setDate(lastDay)
  return date
}

/**
 * get the first date for the specified date according to local time. 
 * @param {*} date 
 */
function firstDayOfMonth(date = new Date()) {
  date.setDate(1)
  return date
}

/**
 * get the last date for the specified date according to local time. 
 * @param {*} date 
 */
function lastDayOfMonth(date = new Date()) {
  var month = date.getMonth() // current month
  var nextMonth = month + 1
  // 如果 dayValue 超出了月份的合理范围，setDate 将会相应地更新 Date 对象。
  // 例如，如果为 dayValue 指定0，那么日期就会被设置为上个月的最后一天。
  date.setMonth(nextMonth, 0)
  return date
}

/**
 * get the first date for the specified date according to local time. 
 * @param {*} date 
 */
function firstDayOfQuarter(date = new Date()) {
  var month = date.getMonth() // current month

  if (month >= 0 && month <= 2) {
    date.setMonth(0, 1)
  }

  if (month >= 3 && month <= 5) {
    date.setMonth(3, 1)
  }

  if (month >= 6 && month <= 8) {
    date.setMonth(6, 1)
  }

  if (month >= 9 && month <= 11) {
    date.setMonth(9, 1)
  }

  return date
}

/**
 * get the last date for the specified date according to local time. 
 * @param {*} date 
 */
function lastDayOfQuarter(date = new Date()) {
  var month = date.getMonth() // current month

  // 如果 dayValue 超出了月份的合理范围，setDate 将会相应地更新 Date 对象。
  // 例如，如果为 dayValue 指定0，那么日期就会被设置为上个月的最后一天。  
  if (month >= 0 && month <= 2) {
    date.setMonth(3, 0)
  }

  if (month >= 3 && month <= 5) {
    date.setMonth(6, 0)
  }

  if (month >= 6 && month <= 8) {
    date.setMonth(9, 0)
  }

  if (month >= 9 && month <= 11) {
    date.setMonth(12, 0)
  }

  return date
}

function firstDayOfYear(date = new Date()) {
  date.setMonth(0, 1)
  return date
}

function lastDayOfYear(date = new Date()) {
  date.setMonth(12, 0)
  return date
}

export default {
  firstDayOfWeek,
  lastDayOfWeek,
  firstDayOfMonth,
  lastDayOfMonth,
  firstDayOfQuarter,
  lastDayOfQuarter,
  firstDayOfYear,
  lastDayOfYear
}



